<!--
[configStart]
{
  "pageInfo": {
    "title": "",
    "keywords": "",
    "description": ""
  }
}
[configEnd]
-->
<template>
  <div v-html="content"></div>
</template>

<script>
import '../utils/rem.js'

export default {
  components: {
  },
  data () {
    return {
      content: '',
    }
  },
  computed: {
  },

  created () {
    this.getDetail()
  },
  mounted () {
  },
  methods: {
    getDetail () {
      this.$apm.fetch({
        url: '/base/protocol',
        method: 'get',
        timeout: 30000,
        param: {
          category: this.$apm.pageData.category || ''
        }
      }).then(res => {
        this.content = res.data.content
        document.title = res.data.title || ''
      }).catch(() => {
      })
    }
  }
}
</script>

<style lang="less">
@import '~@common/assets/css/reset.less';
@import '../assets/css/common.less';
*,
body {
  margin: 0px;
  padding: 0px;
}
.font-family(@font) {
  font-family: 'FZLanTingHei-R-GBK', STHeiti STXihei, Microsoft Yahei, Arial,
    @font;
}
.webkitCss(@key, @value) {
  @{key}: @value;
  -webkit-@{key}: @value;
}

.flex {
  display: flex;
  display: -webkit-flex;
}

body {
  -webkit-font-smoothing: antialiased;
  .font-family('PingFangSC-Medium, PingFang SC');
  background: #fff;
  margin: 0px;
}
</style>



